<template>
  <span>
    <input type="file" id="picture_upload_input" name="picture_upload_input" @change="imgUpload" accept="image/png, image/jpeg" />
    <button v-on:click="triggerPictureUpload">
      <img id="cvPicture" v-bind:src="picturedata" alt="Add your CV picture"  />
    </button>
  <button id="deleteButton" class="deletePicture print_hide" v-on:click="deletePicture"><v-icon color="black" v-bind:title="$t('RemovePicture')">delete_outline</v-icon></button>
  </span>
</template>

<script>


  export default {
    data: () => ({
      picturedata : "#"
    }),
    props : ["picture"],
    created () {
      var picturedata = localStorage.getItem("CVPicture")
      // load picture from localstorage if exists
      if (picturedata) {
      this.picturedata = picturedata
      } else {
        // use template
        this.picturedata = this.picture
      }
    },
    methods : {
      imgUpload(e) {
        var pictureObject = e.target.files[0]
        if (pictureObject) {
          this.$store.state.CVData.CVPicture = pictureObject
          var reader = new FileReader()
          reader.onload = function (e) {
            document.getElementById("cvPicture").src = e.target.result
            localStorage.setItem("CVPicture", e.target.result)
          }
          reader.readAsDataURL(pictureObject)
          document.getElementById("cvPicture").classList.remove("print_hide")
        }
      },
      triggerPictureUpload () {
        // console.log("clicking the file input element")
        document.getElementById("picture_upload_input").click()
      },
      deletePicture () {
        document.getElementById("cvPicture").removeAttribute("src")
        document.getElementById("cvPicture").classList.add("print_hide")
        localStorage.setItem("CVPicture", "#")
      }
    }
  }
</script>

<style scoped>

  #cvPicture {
    max-height: 200px;
    max-width: 200px;
    height: auto;
    width: auto;
  }

  #picture_upload_input {
    display: none;
  }

  .deletePicture {
    float:right;
    padding-left: 2mm;
    padding-right: 2mm;
  }
  
</style>
