<template>
  <div style="width:100%; background-color:light-green;">
    <div class="flexBetween bottomMargin">
      <div class="halfFlex">
        <input class="modelinput company" v-on:blur="hideInput($event,'company')" v-model="sub.company">
        <div class="company" v-on:click="showInput">{{sub.company}}</div>
      </div>
      <div class="halfFlex">
        <input class="modelinput pullRight" v-on:blur="hideInput($event,'location')" v-model="sub.location">
        <div class="pullRight" v-on:click="showInput">{{sub.location}}</div>
      </div>
    </div>
    <div class="flexBetween bottomMargin">
      <div class="halfFlex">
        <input class="modelinput" v-on:blur="hideInput($event,'title')" v-model="sub.title">
        <div v-on:click="showInput">{{sub.title}}</div>
      </div>
      <div class="halfFlex">
        <input class="modelinput pullRight" v-on:blur="hideInput($event,'period')" v-model="sub.period">
        <div class="pullRight" v-on:click="showInput">{{sub.period}}</div>
      </div>
    </div>
    <div class="subSectionContent bottomMargin">
      <ul>
        <!-- <CVSubSectionListPoint  v-for="(item,i) in data.description" :key="i" /> -->
        <li v-for="(item,i) in sub.description" :key="i">
          <input class="modelinput" v-on:blur="hideInput($event,'description',i)" v-model="sub.description[i]">
          <span v-on:click="showInput">{{item}}</span>
        </li>
      </ul>
      <span class="actionbuttons print_hide"> 
        <button v-on:click="newSubSectionLi"><v-icon small color="black" v-bind:title="$t('AddNewLi')">add</v-icon></button>
        <button v-on:click="delSubSectionLi"><v-icon small color="black" v-bind:title="$t('RemoveNewLi')">remove</v-icon></button>
      </span>
    </div>
  </div>
</template>

<script>

  export default {
    data: () => ({
      // company : "",
      // location : "",
      // title : "",
      // period : "",
      // description : []
    }),
    /**
     * copy over data from store into component
     */
    created () {
      // for (var i in this.$data) {
      //   this.$data[i] = this.$store.state.CVData[this.section][this.index][i]
      // }
      // console.log(this.sub)
    },
    props: ["section", "index","sub"],
    methods : {
      showInput(e) {
        var input = e.target.parentElement.firstChild
        input.style.display= "block"
        input.focus()
        e.target.style.display = "none"
      },
      /**
       * hides input element, logs change to localStorage, updates store and localStorage
       */
      hideInput(e,elname, liindex) {
        // hide input
        var innerdiv = e.target.parentElement.childNodes[1]
        e.target.style.display = "none"
        innerdiv.style.display = "block"

        // log changes

        // update store and localStorage
        // console.log(elname)
        // if (liindex) {
        //   // its a description item
        //   this.$store.state.CVData[this.section][this.index][elname][liindex] = this[elname][liindex]
        // } else {
        //   this.$store.state.CVData[this.section][this.index][elname] = this[elname]
        // }
        localStorage.setItem("CVData" , JSON.stringify(this.$store.state.CVData))
        // console.log("set local storage:")
        // console.log(this.$store.state.CVData)
      },
      newSubSectionLi () {
        // console.log(this.$store.state.CVDataTemplate.sections[0].subSections[0].description)
        var templatedata = this.$store.state.CVDataTemplate.sections[0].subSections[0].description[0]
        // update component data, store, localStorage
        // this["description"].push(templatedata) // <- not needed
        // console.log(templatedata)
        this.sub.description.push(templatedata)
        localStorage.setItem("CVData" , JSON.stringify(this.$store.state.CVData))
      },
      delSubSectionLi () {
        // update component data, store, localStorage
        // this["description"].pop() // <- not needed
        if (this.sub.description.length === 0) {
          // console.log("remove entire subsection")
          this.$parent.delSubSection(this.index)
        } else {
          this.sub.description.pop()
        }
        localStorage.setItem("CVData" , JSON.stringify(this.$store.state.CVData))
      }
    }
  }
</script>

