<template>
  <select class="LanguageSwitcher"
          name="language"
          id="LanguageSwitcher"
          @change="changeLanguage">
    <option v-for="lang in supportedLanguages"
            :key="lang"
            :selected="isCurrentLanguage(lang)"
            :class="{ 'is-selected': isCurrentLanguage(lang) }"
            :value="lang">
      {{$t(lang)}}
    </option>
  </select>
</template>
<script>
import { Trans } from '@/plugins/Translation'
export default {
  computed: {
    supportedLanguages () {
      // console.log(Object.keys(this.$i18n.i18next.store.data))
      return Object.keys(this.$i18n.i18next.store.data)
    },
    currentLanguage () {
      return this.$i18n.i18next.language.substr(0,2)
    }
  },
  methods: {
    changeLanguage (e) {
      const lang = e.target.value
      const to = this.$router.resolve({ params: { lang } })
      return this.$i18n.i18next.changeLanguage(lang).then(() => {
        this.$router.push(to.location)
      })
    },
    isCurrentLanguage (lang) {
      return lang === this.$i18n.i18next.language.substr(0,2)
    }
  }
}
</script>