import Vue from 'vue'
import Vuex from 'vuex'

import sampleData from '../public/sampleCVs/ThomasEdison.json'
import templateData from '../public/sampleCVs/template.json'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    CVData : sampleData,
    CVDataTemplate : templateData,
    drawer: null
  },
  mutations: {
    toggleDrawer (state) {
      state.drawer = !state.drawer
    },
    setDrawer (state , e) {
      state.drawer = e
    }
  },
  actions: {

  }
})

