<template>
  <div class="BaseData">
      <input class="modelinput" id="name_input" v-on:blur="hideInput('name')" v-model="basic.name">
      <p id="name_p" v-on:click="showInput('name')">{{basic.name}}</p>
      <input class="modelinput" id="address1_input" v-on:blur="hideInput('address1')" v-model="basic.address1">
      <p id="address1_p" v-on:click="showInput('address1')">{{basic.address1}}</p>
      <input class="modelinput" id="address2_input" v-on:blur="hideInput('address2')" v-model="basic.address2">
      <p id="address2_p" v-on:click="showInput('address2')">{{basic.address2}}</p>
      <br>
      <input class="modelinput" id="telephone_input" v-on:blur="hideInput('telephone')" v-model="basic.telephone">
      <p id="telephone_p" v-on:click="showInput('telephone')">{{basic.telephone}}</p>
      <input class="modelinput" id="email_input" v-on:blur="hideInput('email')" v-model="basic.email">
      <p id="email_p" v-on:click="showInput('email')">{{basic.email}}</p>
      <br>
      <input class="modelinput" id="birthdate_input" v-on:blur="hideInput('birthdate')" v-model="basic.birthdate">
      <p id="birthdate_p" v-on:click="showInput('birthdate')">{{basic.birthdate}}</p>
      <input class="modelinput" id="birthplace_input" v-on:blur="hideInput('birthplace')" v-model="basic.birthplace">
      <p id="birthplace_p" v-on:click="showInput('birthplace')">{{basic.birthplace}}</p>
  </div>
</template>

<script>


  export default {
    data: () => ({
    }),
    props : ["basic"],
    computed : {
      data () {
        return this.$store.state.CVData.basic
      }
    },
    methods : {
      showInput(id) {
        document.getElementById(id+"_input").style.display = "block"
        document.getElementById(id+"_input").focus()
        document.getElementById(id+"_p").style.display = "none"
      },
      hideInput(id) {
        document.getElementById(id+"_input").style.display = "none"
        document.getElementById(id+"_p").style.display = "block"
        localStorage.setItem("CVData" , JSON.stringify(this.$store.state.CVData))
      }
    }
  }
</script>

<style scoped>
  p {
    margin: 0;
  }

  .modelinput {
    display:none;
    width:100%;
  }

  .BaseData {
    text-align:end;
    margin-bottom: 6mm;
  }

  #name_p, #name_input {
    font-weight: 600;
  }

</style>
