<template>
  <v-navigation-drawer
      v-model="drawer"
      fixed
      clipped
      class="print_hide grey lighten-4"
      app
    >
      <v-list
        dense
        class="grey lighten-4"
      >
        <template v-for="(item, i) in items">
          <v-layout
            v-if="item.heading"
            :key="i"
            row
            align-center
          >
            <v-flex xs6>
              <v-subheader v-if="item.heading">
                {{ item.heading }}
              </v-subheader>
            </v-flex>
            <v-flex xs6 class="text-xs-right">
              <v-btn small flat>edit</v-btn>
            </v-flex>
          </v-layout>
          <v-divider
            v-else-if="item.divider"
            :key="i"
            dark
            class="my-3"
          ></v-divider>
          <v-list-tile
            v-else
            :key="i"
            @click="routeTo(item)"
          >
            <v-list-tile-action>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title class="grey--text">
                {{ $t(item.text) }}
              </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </template>
      </v-list>
    </v-navigation-drawer>
</template>

<script>

export default {
  data: () => ({
    }),
    computed : {
      items () { 
          // var= this.i18next.messages[this.$i18n.locale];

          return [
          { icon: 'home' , text:'StartingPage' , link : '/'},
          { icon: 'edit', text:'Editor' , link:'/editor' },
          { icon: 'help_outline', text:'GettingStarted' , link:'/gettingstarted' },
          { icon: 'playlist_add_check', text:'Checklist' , link:'/checklist' },
          { divider: true },
          { icon: 'lightbulb_outline', text:'PrivacyAndCookies' , link:'/privacycookies' },
          { icon: 'lightbulb_outline', text:'Legal' , link:'/legal' },
          // { icon: 'lightbulb_outline', text:'About' , link:'/about' },
          // { icon: 'touch_app', text: 'Reminders' },
          // { heading: 'Labels' },
          // { icon: 'add', text: 'Create new label' },
          // { divider: true },
          // { icon: 'archive', text: 'Archive' },
          // { icon: 'delete', text: 'Trash' },
          // { divider: true },
          // { icon: 'settings', text: 'Settings' },
          // { icon: 'chat_bubble', text: 'Trash' },
          // { icon: 'help', text: 'Help' },
          // { icon: 'phonelink', text: 'App downloads' },
          // { icon: 'keyboard', text: 'Keyboard shortcuts' }
        ]
      },
      drawer: {
        get() {
          return this.$store.state.drawer
        },
        set(e) {
          this.$store.commit("setDrawer",e)
        }
      }
    },
    methods : {
      routeTo(item) {
        if (item.link) {
          this.$router.push(item.link)
        }
      }
    },
    props: {
      source: String
    }
}
</script>

<style>
.v-navigation-drawer__border {
      display: none;
}
</style>
