<template>
    <v-snackbar
      v-model="snackbar"
      bottom right
      :timeout="timeout"
    >
      <div>
        {{$t("CookieText")}}
        <router-link to="/privacycookies">{{$t("CookieTextLearnMore")}}</router-link>
      </div>
      <v-btn
        color="pink"
        flat
        @click="clickedCookieInfo"
      >
        {{$t("CookiesGotIt")}}
      </v-btn>
    </v-snackbar>
</template>

<script>
export default {
    data () {
        return {
            snackbar : false,
            timeout : 0
        }
    },
    methods : {
        clickedCookieInfo() {
            this.snackbar = false
            localStorage.setItem("clickedCookieInfo" , "true")
        }
    },
    created () {
        // check doNotTrack. if enabled, handle.
        var doNotTrack = null;
        if (window.doNotTrack || navigator.doNotTrack || navigator.msDoNotTrack || 'msTrackingProtectionEnabled' in window.external) {
            // The browser supports Do Not Track!
            if (window.doNotTrack == "1" || navigator.doNotTrack == "yes" || navigator.doNotTrack == "1" || navigator.msDoNotTrack == "1" || window.external.msTrackingProtectionEnabled()) {
                doNotTrack = true
                this.snackbar = false
            }
        }
        // display banner if the user never clicked on it's Got It! before. if doNotTrack: dont show the banner at all
        var clickedCookieInfo = localStorage.getItem("clickedCookieInfo")
        if (clickedCookieInfo === "true") {
            this.snackbar = false
        } else {
            if (!doNotTrack) {
                setTimeout(() => {
                    this.snackbar = true
                }, 500)
            }
            
        }
    }
}
</script>

