<template>
  <div class="section">
    <div style="position:relative;">
      <h2 class="title">{{$t(title)}}</h2>
      <span class="actionbuttons print_hide"> 
        <button v-on:click="newSubSection"><v-icon color="black" v-bind:title="$t('AddNewSubSection')">add_circle_outline</v-icon></button>
        <button v-on:click="delSubSection"><v-icon color="black" v-bind:title="$t('RemoveNewSubSection')">remove_circle_outline</v-icon></button>
      </span>
    </div>
    <hr />
    <CVSubSection v-for="(sub,index) in subSections" :key="index" 
    v-bind:sub="sub"
    v-bind:index="index"
     />
  </div>
</template>

<script>
import CVSubSection from '../components/CVSubSection'

import jsonQuery from 'json-query'

  export default {
    data: () => ({
    }),
    computed : {
      // SubSections () {
      //   return this.$store.state.CVData[this.title]
      // }
    },
    methods : {
      newSubSection () {
        // var templatedata = JSON.parse(JSON.stringify(this.$store.state.CVDataTemplate.sections[0].subSections[0]))
        var templateobj = this.$store.state.CVDataTemplate
        var template = jsonQuery('sections[title='+this.title+'].subSections' , {data:templateobj})
        var templatedata = JSON.parse(JSON.stringify(template.value[0]))
        this.subSections.unshift(templatedata)
        localStorage.setItem("CVData" , JSON.stringify(this.$store.state.CVData))
      },
      delSubSection (index) {
        // update component data, store, localStorage
        if (index) {
          this.subSections.splice(index,1)
        } else {
          this.subSections.shift()
        }
        localStorage.setItem("CVData" , JSON.stringify(this.$store.state.CVData))
      }
    },
    components : {
      CVSubSection
    },
    props: ["title","subSections"]
  }
</script>

<style>

  .section {
    width: 100%;
    margin-bottom: 6mm;
  }

  .title {
    font-weight: 300;
    text-transform: uppercase;
    display:inline;
  }
  hr {
    border-width: 0.5mm 0 0 0;
    color: lightgray;
    margin-bottom: 2mm;
  }

  .actionbuttons {
    position: absolute;
    min-width:20mm;
    left:var(--actionbuttons-positioning-left);
