<template>
    <v-toolbar app clipped-left class="print_hide">
      <v-toolbar-side-icon @click="toggleDrawer"></v-toolbar-side-icon>
      <v-toolbar-title class="headline text-uppercase">
        <router-link class="font-weight-light" style="color: inherit; text-decoration:inherit;" to="/">
          <span class="font-weight-light">{{$t("ApplicationNameFull")}}</span>
        </router-link>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <div id="rightSide">
        <span>
          <span>
            {{$t("language")}}:
          </span>
          <TheLanguageSwitcher />
        </span>
        <v-btn flat @click="openPrintDialogue">
          <span class="mr-2">
            <v-icon class="topMenuIcon" color="black" v-bind:title="$t('print')">print</v-icon>{{$t("print")}}
          </span>
        </v-btn>
        <!-- <v-btn flat>
          <span class="mr-2" v-on:click="clearLocalStorage">CLEAR</span>
        </v-btn>
        <v-btn flat>
          <span class="mr-2" v-on:click="saveCV">COPY TO CP</span>
        </v-btn> -->
      </div>
    </v-toolbar>
    
</template>

<script>

import TheLanguageSwitcher from '@/components/TheLanguageSwitcher'

export default {
    data : () => ({}),
    methods: {
        toggleDrawer () {
          this.$store.commit("toggleDrawer")
        },
        openPrintDialogue () {
          window.print()
        },
        saveCV () {
            const el = document.createElement('textarea');
            el.value = JSON.stringify(this.$store.state.CVData);
            el.setAttribute('readonly', '');
            el.style.position = 'absolute';
            el.style.left = '-9999px';
            document.body.appendChild(el);
            el.select();
            document.execCommand('copy');
            document.body.removeChild(el);
        },
        clearLocalStorage () {
          localStorage.removeItem("CVData")
          location.reload()
        }
    },
    components : {TheLanguageSwitcher}
}
</script>

<style scoped>

  .topMenuIcon {
    padding-left:5px;  
    padding-right:5px;  
    vertical-align:middle;
  }
</style>
